.main-container {
  position: relative;
  min-height: 100vh;
  overflow: hidden; }

.main {
  min-height: calc(100vh - 144px); }
  .main__inner {
    padding: 30px; }

.basic-content {
  padding: 30px 0; }

.page {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  background-color: #f8f8f8;
  overflow-y: auto; }
  .page__container {
