.drawer {
  transition: all .35s ease; }

.drawer-enter {
  transform: translateX(100%); }

.drawer-enter-active {
  transform: translateX(0); }

.drawer-exit {
  transform: translateX(100%); }

.collapse {
  margin-bottom: 10px; }
  .collapse__heading {
    cursor: pointer;
    padding: 15px;
    background-color: #fff; }
  .collapse__content {
    overflow: hidden;
    padding: 15px;
    background-color: #fff;
    border-top: 1px solid #e5e5e5;
    transition: all .35s ease; }
    .collapse__content.active {
      height: auto; }

.home-list {
